export default (fun,delay=500,immediate=true)=>{
    let timer=null//‰øùÂ≠òÂÆöÊó∂Âô®
    return function(args){
        let that=this
        let _args=args
        if(timer)clearTimeout(timer)//‰∏çÁÆ°ÊòØÂê¶Á´ãÂç≥ÊâßË°åÈÉΩË¶ÅÊ∏ÖÁ©∫ÂÆöÊó∂Âô®
        if(immediate){
            if(!timer)fun.apply(that,_args)
            //‰∏çÁÆ°‰∏ä‰∏Ä‰∏™Âª∂Êó∂ÊòØÂê¶ÂÆåÊàêÈÉΩÈúÄË¶ÅÈáçÊñ∞ÁΩÆÂÆöÊó∂Âô®
            timer=setTimeout(function(){//Âà∞Êó∂Èó¥ÂêéÔºåÂÆöÊó∂Âô®ÁΩÆnullÔºåÈÅøÂÖçÂÜÖÂ≠òÊ≥ÑÊºè
                timer=null;
            },delay)
        }else{
            timer=setTimeout(function(){
                fun.call(that,_args)
            },delay)
        }
    }

}